import React, { useState } from 'react'
import { Navbar,Container,Nav,NavDropdown,Button } from 'react-bootstrap';
import './App.css';
import Data from './data.js';
import Detail from './Detail';
import axios from 'axios';

import { Link, Route, Switch } from 'react-router-dom';


function App() {

  let [shoes, shoes변경] = useState(Data);
  let [재고, 재고변경] = useState([10,11,12]);
  let [로딩, 로딩변경] = useState(false);
  let [카운트, 카운트변경] = useState(1);






  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">ShoeShop</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/">Home</Nav.Link>
              <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>


      <Switch>

        <Route exact path="/">
          <div className="jumbotron">
            <h1>20% Season OFF</h1>
            <p>dsadsadsadasad</p>
            <Button>Button</Button>
          </div>
          <div className="container">
            <div className="row">
              {
                shoes.map((a,i)=>{
                  return <Card shoes={shoes[i]} i={i} key={i}/>
                })
              }
            </div>
            
              {
                로딩 === true
                ?<Loading/>
                :null
              }


            {
              카운트 === 2
              ? null
              :
              <button className="btn btn-primary" onClick={()=>{
                // 서버에 데이터를 보내고 싶을때 post요청하는 법
                // axios.post('서버URL', {전달할 데이터});
  
                // 카운트 변경
                카운트변경(카운트+1)


                // 로딩중화면
                로딩변경(true)
  
                
  
                axios.get('https://codingapple1.github.io/shop/data'+ (카운트+1) +'.json')
                .then((result)=>{ // ajax성공하면 실행할 코드를 작성
                  console.log(result.data);
                  shoes변경([...shoes, ...result.data]);
  
                  // 로딩중 UI 없애는 코드
                  로딩변경(false)
  
                })
                .catch(()=>{ // ajax실패시 실행할 코드 작성
                  console.log('실패했어요')
  
                  // 로딩중 UI 없애는 코드
                  로딩변경(false)
                })
              
              }}>더보기</button>
            }            



            

          </div>
        </Route>

        <Route path="/detail/:id">
          <Detail shoes={shoes} 재고={재고} 재고변경={재고변경}/>
        </Route>

        <Route path="/:id">
          <div>아무거나 적었을때 이거 보여주세요</div>
        </Route>

      </Switch>

    </div>
  );
}



function Card(props){
  return(
    <div className="col-md-4">
      <img src={'http://codingapple1.github.io/shop/shoes'+ (props.i+1)  +'.jpg'} width="100%" />
      <h4>{ props.shoes.title }</h4>
      <p>{ props.shoes.content }</p>
      <p>{ props.shoes.price }</p>
  </div>
  )
}

function Loading(){
  return (
    <div className="loading">
      <p>로딩중입니다</p>
    </div>
  )
}

export default App;
